@import url(https://fonts.googleapis.com/css2?family=Indie+Flower&family=Pangolin&display=swap);
@import url(https://cdn.jsdelivr.net/npm/bootstrap@4.5.3/dist/css/bootstrap.min.css);
@import url(https://cdnjs.cloudflare.com/ajax/libs/animate.css/4.1.1/animate.min.css);
body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
		'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
		sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
		monospace;
}

body {
	background-color: #cdd7d6;
	font-family: 'Pangolin', cursive;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	color: #f87060;
}

h1,
h2,
h3,
h4,
h5,
h6 a {
	color: #f87060;
}

p.header {
	color: #102542;
	width: 650px;
}

label {
	color: #f87060;
	font-family: 'Josefin Sans', sans-serif;
}

/* Column Layouts */

.logo {
	width: 75px;
	height: 75px;
}

.top {
	color: #102542;
}

ul.menu {
	list-style-type: none;
	margin: 0;
	padding: 0;
	overflow: hidden;
	background-color: #f87060;
}

ul.thumbnail {
	list-style-type: none;
	margin: 0;
	padding: 0;
	overflow: hidden;
	background-color: #cdd7d6;
}

ul.subtotal {
	display: inline-block;
	float: none;
}

li.subtotal {
	display: inline-block;
	float: none;
}

li.menu {
	float: left;
}

li a {
	display: block;
	color: #102542;
	text-align: center;
	padding: 16px;
	text-decoration: none;
}

li a:hover {
	background-color: #f87060;
}

/* Product Page */

img.hero {
	width: 98.5%;
}

.divider {
	border-right: 5px solid #f87060;
}

img.thumbnail {
	float: left;
	margin-top: 0.7em;
	margin-right: 0.7em;
	width: 10.2em;
}

img.thumbnail:last-child {
	float: left;
	margin-top: 0.7em;
	width: 10.5em;
}

li.thumbnail {
	float: left;
	margin-top: 0.7em;
	margin-right: 0.7em;
}

li.thumbnail:last-child {
	float: left;
	margin-top: 0.7em;
	width: 10.5em;
}

img {
	width: 200px;
}

/* Checkout */

.display_blocks {
	background-color: #fff;
	border-top: 1px solid #eee;
	border-left: 1px solid #eee;
	border-right: 1px solid #eee;
	padding: 16px;
}

._content_1oahw6 {
	max-width: 640px;
	margin: 0 auto;
}

.center {
	text-align: center;
}

/* Shopping Cart */

.cart {
	width: 100%;
	background-color: #cdd7d6;
	border-top: 2px solid #f87060;
	border-bottom: 2px solid #f87060;
	padding: 10px;
}

.cart thead {
	border-top: 2px solid #f87060;
}

.cart tbody {
	text-align: center;
}

.order-summary {
	width: 100%;
	background-color: #fff;
	padding: 10px 20px;
}

.order-summary .call-to-action {
	width: 90%;
	display: inline-block;
	text-align: center;
}

/* Bootstrap Override */

.btn-primary:hover,
.btn-primary {
	color: #b3a394;
	background-color: #102542;
	border-color: #102542;
	display: block;
	padding: 10px 5px;
	text-align: center;
	text-decoration: none;
	width: 200px;
	border-radius: 8px;
	margin-left: 20px;
}

.btn-primary:hover {
	background-color: #102542;
}

.btn.btn-primary:active {
	background-color: #102542 !important;
}

